import React from "react"
import { Button, PopoutWrapper, Alert } from "@vkontakte/vkui"
import { useDispatch } from "react-redux"
import { closePopout } from "../store/router/actions"

const AlertOffline = () => {
  const dispatch = useDispatch()
  return (
    <Alert
      actions={[
        {
          title: "",
          autoclose: true,
          mode: "cancel",
        },
      ]}
      header='Offline'
      onClose={() => dispatch(closePopout())}
    >
        !   ,   
      ,     
    </Alert>
  )
}
export default AlertOffline
