import bridge from "@vkontakte/vk-bridge"
import { ADD_HINT, SET_UNCOMPLITED_GAME } from "../game/actionsType"
import { openSnackbar } from "../router/actions"

import { GET_FETCHED_USER, GET_USER_DATA, SET_SCHEME } from "./actionTypes"

export const setScheme = (scheme) => {
  if (scheme == "bright_light") {
    bridge.send("VKWebAppSetViewSettings", {
      status_bar_style: "dark",
      action_bar_color: "#fff",
    })
  } else {
    bridge.send("VKWebAppSetViewSettings", {
      status_bar_style: "light",
      action_bar_color: "#191919",
    })
  }
  return { type: SET_SCHEME, payload: scheme }
}

export const getFetchedUser = () => {
  return (dispatch) => {
    bridge
      .send("VKWebAppGetUserInfo")
      .then((data) => dispatch({ type: GET_FETCHED_USER, payload: data }))
  }
}

export const getUserData = () => {
  return (dispatch) => {
    bridge
      .send("VKWebAppStorageGet", {
        keys: ["statistics", "resolvedWords", "game"],
      })
      .then((data) => {
        let result = {}
        let game = {}

        for (let _key of data.keys) {
          if (_key.key === "game") {
            game = JSON.parse(_key.value)
            continue
          }
          result = { ...result, [_key.key]: JSON.parse(_key.value) }
        }
        console.log("game ", game)

        dispatch({ type: SET_UNCOMPLITED_GAME, payload: game.activeGame })
        dispatch({ type: ADD_HINT, payload: game.hints })

        return dispatch({ type: GET_USER_DATA, payload: result })
      })
      .catch((err) => console.log(err))
  }
}
export const addHints = () => {
  return (dispatch) => {
    if (bridge.supports("VKWebAppShowNativeAds")) {
      bridge
        .send("VKWebAppShowNativeAds", { ad_format: "reward" })
        .then((data) => {
          if (data.result) {
            dispatch({ type: ADD_HINT, payload: 10 })
            return dispatch(
              openSnackbar(" ", "winning", 3000)
            )
          }

          dispatch(openSnackbar(" ", "losing"))
        })
        .catch((err) => console.log("add", err))
    } else {
      dispatch(openSnackbar("    ", "losing"))
    }
  }
}
export const showAds = () => {
  if (bridge.supports("VKWebAppShowNativeAds")) {
    bridge.send("VKWebAppShowNativeAds", { ad_format: "interstitial" })
  }
}
